.icon {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #101828;
  transition: stroke 0.3s ease-in-out;
}
.propertyCard {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  width: 526px;
}
.cartItem {
  display: inline-flex;
  padding: 12px 18px;
  align-items: center;
  gap: 8px;
  border-radius: 100px;
  background: #f2f4f7;
  color: #101828;
}
